.app-bar-light {
    background-color: #55CCFF !important;
}
.app-bar-dark {
    background-color: #000A23 !important;
}
.app-preview-dark {
    background-color: #000A23 !important;
}

.app-preview-light {
    background-color: #ffffff !important;
}

.day-night-row-container-light {
    background: -webkit-gradient(linear, left bottom, left top, from(#FFFFFF), to(#55ccff));
    background: -webkit-linear-gradient(bottom, #FFFFFF, #55ccff);
    background: -o-linear-gradient(bottom, #FFFFFF, #55ccff);
    background: linear-gradient(0deg, #FFFFFF, #55ccff);
}

.day-night-row-container-dark {
    background: -webkit-gradient(linear, left bottom, left top, from(#181f4d), to(rgba(0, 10, 35, 1.00)));
    background: -webkit-linear-gradient(bottom, #181f4d, rgba(0, 10, 35, 1.00));
    background: -o-linear-gradient(bottom, #181f4d, rgba(0, 10, 35, 1.00));
    background: linear-gradient(0deg, #181f4d, rgba(0, 10, 35, 1.00));
}

.content-light {
    background-color: #F1F1F1;
}

.content-dark {
    background-color: #000A23;
}

.typographic-color-light {
    color: black !important;
}

.typographic-color-light input {
    color: black !important;
}

.typographic-color-dark {
    color: white !important;
}

.typographic-color-dark input {
    color: white !important;
}

.avatar-container-light {
    background-color: #55CCFF !important;
}

.avatar-container-dark {
    background-color: #181F4E !important;
}

.message-content-dark {
    background-color: #0A6AA5 !important;
}

.message-content-light {
    background-color: #66BB66 !important;
}

.date-time-item-dark {
    background-color: #555555 !important;
}

.date-time-item-light {
    background-color: #555555 !important;
}

.location-dark {
    background-color: #EE4444 !important;
}

.location-light {
    background-color: #EE4444 !important;
}

.host-invite-dark {
    background-color: #DDDDDD !important;
}

.host-invite-light {
    background-color: #DDDDDD !important;
}

.host-invite-avatar-light {
    background-color: #999999 !important;
}

.host-invite-avatar-dark {
    background-color: #333333 !important;

}

.message-container-user-edit div {
    -webkit-box-shadow: unset !important;
            box-shadow: unset !important;
}

.message-container-user-edit>div:first-of-type {
    -webkit-box-shadow: unset !important;
            box-shadow: unset !important;
}

.event-title-light input {
    color: black !important;
}

.event-title-dark input {
    color: white !important;
}

.light-theme__dialog .MuiPaper-root{
    background-color: #ffffff !important
}
.dark-theme__dialog .MuiPaper-root{
    background-color: #000A23 !important
}
.light-theme__dialog .MuiTypography-root, input{
    color: black !important
}
.light-theme__dialog input{
    color: black !important
}
.dark-theme__dialog .MuiTypography-root, input{
    color: white !important
}
.dark-theme__dialog input{
    color: white !important
}

.dark-theme__dialog .status{
    color: white !important
}
.light-theme__dialog .status{
    color: black !important
}


