.input-field-container{
    height: 80px;
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    border-top: 1px solid #ddd;
}
.input-field-container > div{
    width: 100%;
    height: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
}
.input-field-container input{
    text-align: center;
}
.input-field__event-time{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
}
.input-field__event-time > div{
    width: 49%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
}

.input-field-container .MuiInput-underline{
    border: none!important;
}
.input-field-container .MuiInput-underline::before{
    border: none!important;
}
.input-field-container .MuiInput-underline::after{
    border: none!important;
}

.input-field__text-input{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}
.input-field__text-input .MuiInputBase-root{    
    -ms-flex-positive: 1;    
        flex-grow: 1;
}

.input-field-container .MuiSnackbar-root{
    left: 0px;
}


