.loader-container{
    display: -ms-flexbox;
    display: flex;
    height: 100%;
    width: 100%;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}
.loader {
    width: 100%;
    height: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center; 
}
#ld {
    position: relative;
    -webkit-animation: outercontainer 4s linear infinite;
            animation: outercontainer 4s linear infinite; 
}
#ld div {
    height: 12px;
    width: 12px;
    border-radius: 50%;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0; 
}
#ld div:nth-child(1) {
    top: 20px;
    background: #59CD90;
    -webkit-animation: ld_div1 2s linear infinite;
            animation: ld_div1 2s linear infinite; 
}
#ld div:nth-child(2) {
    top: -20px;
    background: #D91E36;
    -webkit-animation: ld_div2 2s linear infinite;
            animation: ld_div2 2s linear infinite; 
}
#ld div:nth-child(3) {
    left: 20px;
    background: #F39237;
    -webkit-animation: ld_div4 2s linear infinite;
            animation: ld_div4 2s linear infinite; 
}
#ld div:nth-child(4) {
    left: -20px;
    background: #0072BB;
    -webkit-animation: ld_div3 2s linear infinite;
            animation: ld_div3 2s linear infinite; 
}
@-webkit-keyframes outercontainer {
    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg); 
    } 
}
@keyframes outercontainer {
    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg); 
    } 
}
@-webkit-keyframes ld_div1 {
    0% {
        top: 20px; 
    }
    25% {

        top: 0; 
    }
    50% {
        top: 20px;
    }
    75% {
        top: 0; 
    }
